<template>
  <section class="col s4 lucky-number {{class}}">
    <span class="hint">
      {{  $t("lucky-number") }}
    </span>
    {{{ number }}}
  </section>
</template>

<script>
  import {store} from '../../stores/store';

  export default {
    name: 'LuckyNumber',

    props: {
      number: {
        type: Number,
        default: '?'
      },
      class: {
        type: String
      }
    },

    methods: {
      update() {
        store.getLuckyNumber().then(number => {
          if(!number.valid) {
            throw "Invalid";
            return;
          }

          this.number = number.data.number;
          let opts = store.getOptions();
          this.class = opts.number == this.number ? 'active' : '';
        }).catch((e) => {
          this.class = '';
          this.number = '?';
          setTimeout(() => {
            this.update();
          },10000)
        });
      }
    },

    created() {
      this.update();
      store.on("options-update", ()=>{
        this.update();
      })
    }
  }
</script>

<style lang="scss">
  @import "~materialize-css/sass/components/color";

  .lucky-number.active {
    background: color("green", "lighten-3");
  }

  .lucky-number {
    background: color("red", "lighten-2");
  }
</style>



