import Vue from 'vue';
import Router from 'vue-router';
import App from './components/App.vue';
import MainView from './components/main/View.vue';
import SettingsView from './components/settings/View.vue';
const i18n = require('vue-i18n');

if(navigator.serviceWorker) {
  navigator.serviceWorker.register('./service-worker.js').then((registration) => {
    registration.pushManager.subscribe({
      userVisibleOnly: true
    }).then((subscription) => {

    }).catch((err) => {
      console.log("Error occured while registering to push-notifications.", err);
    });
    console.log('Registered service-worker successfully ', registration.scope);
    /**registration.periodicSync.register({
      tag: 'get-lucky-number',
      minPeriod: 0 //1000 * 60 * 60
    }).then((reg) => {
      console.log("successfully registered get-lucky-number sync");
    }).catch((error)=> {
      console.log("trouble registering get-lucky-number sync ", error);
    });**/
  }).catch((err)=> {
    console.log('Error occured while registering service-worker: ', err);
  });
}

require("../scss/style.scss");

//Vue
Vue.use(Router);
Vue.use(i18n, {
  lang: 'pl',
  locales: {
    pl: require("./locales/pl.json")
  }
});

var router = new Router();

router.map({
  '/': {
    component: MainView
  },
  '/settings': {
    component: SettingsView
  }
});

router.beforeEach(()=>{
  window.scrollTo(0,0);
});

router.start(App, '#app');



