<template>
  <section class="col s8 next-class">
    <span class="hint">
      {{ $t("next-class") }}
    </span>
    <a v-if="!nextClass" v-link="{path: '/settings'}"><i class="mdi-action-settings"></i></a>
    {{ nextClass.name }} {{ nextClass.room }}
  </section>
</template>

<script>
  import {
    store
  }
  from '../../stores/store';

  export default {
    name: 'NextClass',

    props: ["nextClass"],

    created() {
      this.update();
      store.on("options-update", ()=>{
        this.update();
      })
    },

    methods: {
      update() {
        let opts = store.getOptions();
        if (!opts.group) {
          return;
        }
        store.getTimetable(opts.group).then((timetable) => {
          timetable = timetable.data.classes;
          store.getHours().then((hours) => {
            hours = hours.data.hours;

            let now = new Date();
            now.setHours(12);
            now.setMinutes(51);
            now.setMonth(11);
            for (let i = 0; i < hours.length; i++) {
              if (now.getMinutes() + now.getHours() * 60 < hours[i].from) {
                if (timetable[now.getDay() - 1] && timetable[now.getDay() - 1][i]) {
                  this.nextClass = timetable[now.getDay() - 1][i];
                  setTimeout(() => {
                    this.update();
                  }, (hours[i].from - now.getMinutes() + now.getHours() * 60) * 1000 * 60 + 2000);
                  return;
                }
              }
            }
            this.nextClass = {
              name: "localhost",
              room: "::1"
            };
          });
        });

      }
    }
  }
</script>



